.action-items__container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: #e8dcdc;
    padding-top: 0.5rem;
    min-height: 10vh;
}

.action-items__header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin-bottom: 11px;
}

.action-items__body {
    margin-left: 1rem;
}

.header__input {
    height: 1.5rem;
    font-size: 1rem;
}

.header__input:focus {
    outline: none;
}

.action-items__header__button {
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;

    /*margin: 10px;*/
    /*padding: 12px 12px;*/

    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: all 150ms linear;
    -o-transition: all 150ms linear;
    transition: all 150ms linear;
    text-align: center;
    white-space: nowrap;
    text-decoration: none !important;
    text-transform: none;
    text-transform: capitalize;

    color: #fff;
    border: 0 none;
    border-radius: 2px;

    font-size: 0.9rem;
    font-weight: 500;
    line-height: 1.3;

    -webkit-appearance: none;
    -moz-appearance:    none;
    appearance:         none;

    -ms-flex-pack: center;

        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 0 0 160px;
        flex: 0 0 160px;

    -webkit-box-shadow: 2px 5px 10px #e4e4e4;

            box-shadow: 2px 5px 10px #e4e4e4;


    color: #202129;
    background-color: #f2f2f2;
    height: 2rem;
}

.action-items__header__button:hover {
    -webkit-transition: all 150ms linear;
    -o-transition: all 150ms linear;
    transition: all 150ms linear;

    opacity: .85;

     color: #202129;
     background-color: #e1e2e2;
     opacity: 1;


}

.action-items__header__button:active {
    -webkit-transition: all 150ms linear;
    -o-transition: all 150ms linear;
    transition: all 150ms linear;
    opacity: .75;

    background-color: #d5d6d6;
    opacity: 1;
}

.action-items__header__button:focus {
    outline: 1px dotted #959595;
    outline-offset: -4px;
}


