.panel {
    -ms-flex: 1 1;
        flex: 1 1;
    /*height: 100vh;*/
    height: 90vh;
}

.panel__cards {
    overflow-y: hidden;
    height: calc(100% - 11em);
}

.panel__cards:hover {
    overflow-y: auto;
}

.center-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 2rem;
}

.panel-text__input {
    border: none;
    border-radius: 1px;
    color: white;
    -ms-flex: 1 1;
        flex: 1 1;
    height: 4rem;
    padding-left: 1rem;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
    border-radius: 1px;
    font-family: 'Noto Sans', sans-serif;
    font-size: 1rem;
    /*box-shadow: 0px 0px 8px -3px #000000;*/
}

.panel-text__input::-webkit-input-placeholder {
    color: white;
}

.panel-text__input:-ms-input-placeholder {
    color: white;
}

.panel-text__input::-ms-input-placeholder {
    color: white;
}

.panel-text__input::placeholder {
    color: white;
}

.panel-text__input:focus {
    outline: none;
    -webkit-box-shadow: 0 0 10px #719ECE;
            box-shadow: 0 0 10px #719ECE;
}


